@import url(https://fonts.googleapis.com/css?family=Alatsi|Roboto:400,400i,700,700i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-size: 1.6em; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body {
  font-family: "Roboto", sans-serif; }

h1, h2, h3, h4, p {
  margin-top: 0; }

p {
  line-height: 1.4; }

html {
  background-color: black; }

body {
  width: 50%;
  margin: auto;
  background-color: white;
  margin-top: 20px; }

h1 {
  background-color: #f7db08e3;
  padding: 6px 16px;
  margin: 0; }

.balance {
  display: inline-block;
  margin: 0;
  padding: 0; }
  .balance p {
    padding-left: 16px;
    display: inline; }
  .balance button {
    margin: 12px;
    padding: 1px;
    font-size: 0.8em; }

table {
  margin-left: 16px;
  border: solid 1px black; }
  table th {
    background-color: #8AF4AC;
    padding: 5px;
    border: solid 1px black; }
  table tr:nth-child(even) {
    background-color: #e1e1e1; }
  table td {
    padding: 5px;
    border: solid 1px black; }
  table tfoot {
    background-color: #C7EFF5; }
    table tfoot .total {
      text-align: right; }

p {
  padding-left: 16px;
  display: inline; }

form {
  margin: 16px; }
  form .purchaseNum {
    width: 36px; }

footer {
  margin: 0;
  padding-bottom: 10px; }
  footer .footer {
    margin: 0;
    padding: 0; }

.ReactModal__Content {
  margin: 0 auto;
  width: 258px;
  height: 168px; }
  .ReactModal__Content input {
    width: 86px; }
  .ReactModal__Content p {
    margin-top: 30px;
    padding-top: 20px;
    color: red; }
  .ReactModal__Content button {
    margin: 8px 2px 0px 2px; }

