table{
    margin-left: 16px;
    border: solid 1px black;
    th{
        background-color: $lightgreen;
        padding: 5px;
        border: solid 1px black;
    }
    tr:nth-child(even){
background-color:$grey;
    }
    td{
        padding: 5px;
        border: solid 1px black;
    } 
    tfoot{
        background-color:$lightblue;
        .total{
            text-align: right;
        }
    }
 }
p{
    padding-left: 16px;
    display: inline;
} 